	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The Dark Side","date":"06 May 2004","slug":"/2004-05-06-the-dark-side/","tags":"Y2004"},"html":"<p>I now have one foot in the realm of evil... I'm blogging this on a 500 MHz 15\" G4 PowerBook, obtained on eBay, running OS X 10.3.3. Mind-altering Mac user Kool-Aid was <em>not</em> included.</p>\n<p><img alt=\"g4_firstpost.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/05/g4_firstpost.jpg\" width=\"320\" height=\"240\" border=\"0\" /></p>"},"next":{"fields":{"title":"Heiko Hebig to Bring TypePad to Germany","date":"03 May 2004","slug":"/2004-05-03-heiko-hebig-to-bring-typepad-to-germany/"}},"previous":{"fields":{"title":"Trial Marriage","date":"08 May 2004","slug":"/2004-05-08-trial-marriage/"}}},{"node":{"fields":{"title":"Heiko Hebig to Bring TypePad to Germany","date":"03 May 2004","slug":"/2004-05-03-heiko-hebig-to-bring-typepad-to-germany/","tags":"Y2004"},"html":"<p>While I was trying to sleep off my jet lag this afternoon, Heiko announced that he will be <a title=\"I am blogging this by Heiko Hebig | hebig.com\" href=\"http://www.hebig.com/archives/002049.shtml\">representing Six Apart in Germany</a> and helping to launch TypePad here. Is there <a href=\"http://nico.blogg.de/eintrag.php?id=194\">room</a> for two <a href=\"http://www.blogg.de\">blogging</a> <a href=\"http://www.typepad.com\">companies</a> in Hamburg, both seeking world domination? I think so.</p>"},"next":{"fields":{"title":"Return","date":"02 May 2004","slug":"/2004-05-02-return/"}},"previous":{"fields":{"title":"The Dark Side","date":"06 May 2004","slug":"/2004-05-06-the-dark-side/"}}},{"node":{"fields":{"title":"Return","date":"02 May 2004","slug":"/2004-05-02-return/","tags":"Y2004"},"html":"<p>We've made it safely back to cool and misty Hamburg. We'd love to spend the day sleeping off the jet lag, but that is a luxury reserved for non-parents, it seems.</p>"},"next":{"fields":{"title":"For Sale: iTunes Prepaid Card","date":"02 May 2004","slug":"/2004-05-02-for-sale-itunes-prepaid-card/"}},"previous":{"fields":{"title":"Heiko Hebig to Bring TypePad to Germany","date":"03 May 2004","slug":"/2004-05-03-heiko-hebig-to-bring-typepad-to-germany/"}}}],"pathPrefix":"","first":false,"last":false,"index":493,"pageCount":1101}}

